export function card2circleLink(seal_pic_list, unit_pixel) {
    // console.log("seal_data in card2circleLink", seal_pic_list)
    d3.select('#card2circle-link-svg').selectAll('g').remove()
    let eventDiv = document.getElementById('seal-card-container') // container

    let link_group = d3.select('#card2circle-link-svg').append('g').attr('class', 'card2circle-group')

    // 
    let link_svg_offset = $('#card2circle-link-svg').offset(), // render line link svg
        circle_svg_offset = $('.seal-circle-svg').offset(), // seal circle svg
        seal_card_svg_offset = $('.seal-card-container').offset() // seal information card container

    // 
    let pathData = d3.path()

    // radius
    const inflexion_radius = unit_pixel

    // seal card
    for (let i in seal_pic_list) {
        const seal = seal_pic_list[i]
        // console.log(seal, `#${seal['seal_name']}-circle`, $(`#${seal['seal_name']}-circle`).offset(), $(`#${seal['seal_name']}-circle`).width(), d3.select(`#${seal['seal_name']}-circle`).node().getBBox().width)
        let card_pos = $(`#seal-card-${seal['seal_name']}-${seal['index']}`).offset(), // jQueryoffset
            circle_pos = $(`#${seal['seal_name']}-circle`).offset()
        let card_point_pos = {
            top: card_pos.top + $(`#seal-card-${seal['seal_name']}-${seal['index']}`).height() + unit_pixel * 2,
            left: card_pos.left + $(`#seal-card-${seal['seal_name']}-${seal['index']}`).width() / 2
            },
            circle_point_pos = {
                top: circle_pos.top,
                left: circle_pos.left + d3.select(`#${seal['seal_name']}-circle`).node().getBBox().width / 2 // jQuery$(`#${seal['seal_name']}-circle`).width()
            }
        let average_point_pos = {
                top: (card_point_pos.top + circle_point_pos.top) / 2 + Math.random() * unit_pixel,
                left: (card_point_pos.left + circle_point_pos.left) / 2
            }
        let link_pos // 

        // line
        let ifRender = !(card_point_pos.left < seal_card_svg_offset.left || card_point_pos.left > seal_card_svg_offset.left + $('.seal-card-container').width() // seal card containerlink
                        || circle_point_pos.left < seal_card_svg_offset.left || circle_point_pos.left > seal_card_svg_offset.left + $('.seal-card-container').width()) // circle
        if (ifRender) {
            // 
            link_pos = [
                { // 
                    x: card_point_pos.left - link_svg_offset.left,
                    y: card_point_pos.top - link_svg_offset.top
                }, { // arc1 start
                    x: card_point_pos.left - link_svg_offset.left,
                    y: average_point_pos.top - link_svg_offset.top - inflexion_radius
                }, { // arc1 end
                    x: (card_point_pos.left - circle_point_pos.left) > 0 ? card_point_pos.left - inflexion_radius - link_svg_offset.left : card_point_pos.left + inflexion_radius - link_svg_offset.left,
                    y: average_point_pos.top - link_svg_offset.top
                }, { // arc2 start
                    x: (card_point_pos.left - circle_point_pos.left) > 0 ? circle_point_pos.left + inflexion_radius - link_svg_offset.left : circle_point_pos.left - inflexion_radius - link_svg_offset.left,
                    y: average_point_pos.top - link_svg_offset.top
                }, { // arc2 end
                    x: circle_point_pos.left - link_svg_offset.left,
                    y: average_point_pos.top - link_svg_offset.top + inflexion_radius
                }, { // end
                    x: circle_point_pos.left - link_svg_offset.left,
                    y: circle_point_pos.top - link_svg_offset.top
                }
            ]

            pathData.moveTo(link_pos[0].x, link_pos[0].y) // 
            pathData.lineTo(link_pos[1].x, link_pos[1].y)
            pathData.arcTo(link_pos[1].x, link_pos[2].y, link_pos[2].x, link_pos[2].y, inflexion_radius) // 
            pathData.lineTo(link_pos[3].x, link_pos[3].y)
            pathData.arcTo(link_pos[4].x, link_pos[3].y, link_pos[4].x, link_pos[4].y, inflexion_radius) // 
            pathData.lineTo(link_pos[5].x, link_pos[5].y)

            // SVG
            d3.selectAll(`#${seal['seal_name']}-card2circle-${seal['index']}`).remove()
            link_group.append("path")
                        .attr("d", pathData.toString()) // 
                        .attr('id', `${seal['seal_name']}-card2circle-${seal['index']}`)
                        .attr("stroke", "#D3BEAF") // 
                        .attr("stroke-width", 0.5) // 
                        .attr("fill", "none") // 
                        // .style('opacity', 0.5)
                        // .style('visibility', () => {
                        //     if (card_pos.left < myDiv.getBoundingClientRect().left || card_pos.left + 0.6 * rem > myDiv.offsetWidth + myDiv.getBoundingClientRect().left) { // 0.3
                        //         return 'hidden'
                        //     } else {
                        //         return 'visible'
                        //     }
                        // })
        }

        // event card
        eventDiv.addEventListener('scroll', function() { // scroll
            // console.log('seal card container ', d3.selectAll(`#${seal['seal_name']}-card2circle-${seal['index']}`))
            // d3.selectAll(`#${seal['seal_name']}-card2circle-${seal['index']}`).remove()

            // card_pos
            card_pos = $(`#seal-card-${seal['seal_name']}-${seal['index']}`).offset() // jQueryoffset
                circle_pos = $(`#${seal['seal_name']}-circle`).offset()
            card_point_pos = {
                top: card_pos.top + $(`#seal-card-${seal['seal_name']}-${seal['index']}`).height(),
                left: card_pos.left + $(`#seal-card-${seal['seal_name']}-${seal['index']}`).width() / 2
            }
            circle_point_pos = {
                top: circle_pos.top,
                left: circle_pos.left + d3.select(`#${seal['seal_name']}-circle`).node().getBBox().width / 2 // jQuery$(`#${seal['seal_name']}-circle`).width()
            }
            average_point_pos = {
                top: (card_point_pos.top + circle_point_pos.top) / 2 + Math.random() * unit_pixel,
                left: (card_point_pos.left + circle_point_pos.left) / 2
            }

            ifRender = !(card_point_pos.left < seal_card_svg_offset.left || card_point_pos.left > seal_card_svg_offset.left + $('.seal-card-container').width() // seal card containerlink
                       || circle_point_pos.left < seal_card_svg_offset.left || circle_point_pos.left > seal_card_svg_offset.left + $('.seal-card-container').width()) // circle

            if (!ifRender) { // link remove
                d3.selectAll(`#${seal['seal_name']}-card2circle-${seal['index']}`).remove()
            } else {
                // 
                link_pos = [
                    { // 
                        x: card_point_pos.left - link_svg_offset.left,
                        y: card_point_pos.top - link_svg_offset.top
                    }, { // arc1 start
                        x: card_point_pos.left - link_svg_offset.left,
                        y: average_point_pos.top - link_svg_offset.top - inflexion_radius
                    }, { // arc1 end
                        x: (card_point_pos.left - circle_point_pos.left) > 0 ? card_point_pos.left - inflexion_radius - link_svg_offset.left : card_point_pos.left + inflexion_radius - link_svg_offset.left,
                        y: average_point_pos.top - link_svg_offset.top
                    }, { // arc2 start
                        x: (card_point_pos.left - circle_point_pos.left) > 0 ? circle_point_pos.left + inflexion_radius - link_svg_offset.left : circle_point_pos.left - inflexion_radius - link_svg_offset.left,
                        y: average_point_pos.top - link_svg_offset.top
                    }, { // arc2 end
                        x: circle_point_pos.left - link_svg_offset.left,
                        y: average_point_pos.top - link_svg_offset.top + inflexion_radius
                    }, { // end
                        x: circle_point_pos.left - link_svg_offset.left,
                        y: circle_point_pos.top - link_svg_offset.top
                    }
                ]

                pathData = d3.path() // pathData(pathDataappend...)
                pathData.moveTo(link_pos[0].x, link_pos[0].y) // 
                pathData.lineTo(link_pos[1].x, link_pos[1].y)
                pathData.arcTo(link_pos[1].x, link_pos[2].y, link_pos[2].x, link_pos[2].y, inflexion_radius) // 
                pathData.lineTo(link_pos[3].x, link_pos[3].y)
                pathData.arcTo(link_pos[4].x, link_pos[3].y, link_pos[4].x, link_pos[4].y, inflexion_radius) // 
                pathData.lineTo(link_pos[5].x, link_pos[5].y)

                // SVG
                d3.selectAll(`#${seal['seal_name']}-card2circle-${seal['index']}`).remove()
                link_group.append("path")
                            .attr("d", pathData.toString()) // 
                            .attr('id', `${seal['seal_name']}-card2circle-${seal['index']}`)
                            .attr("stroke", "#8F7B6C") // 
                            .attr("stroke-width", 0.5) // 
                            .attr("fill", "none") // 
                            .style('opacity', 0.5)
                            // .style('visibility', () => {
                            //     if (card_pos.left < myDiv.getBoundingClientRect().left || card_pos.left + 0.6 * rem > myDiv.offsetWidth + myDiv.getBoundingClientRect().left) { // 0.3
                            //         return 'hidden'
                            //     } else {
                            //         return 'visible'
                            //     }
                            // })
            }
                            
        })
    }
}